"use client";

import React, { useState, useRef, RefObject, ChangeEvent, createRef, useEffect } from 'react';
import * as htmlToImage from 'html-to-image';
import download from 'downloadjs';
import { Header } from '@/components/header';
import { TemplateCard } from '@/components/template-card';
import { layouts, type SlideContent } from '@/components/layouts';
import { PostTemplate } from '@/components/post-template';
import { Button } from '@/components/ui/button';

export default function Home() {
  const [activeLayoutKey, setActiveLayoutKey] = useState('launchAnnouncement');
  const [slides, setSlides] = useState<SlideContent[]>(layouts[activeLayoutKey].slides);
  const [images, setImages] = useState<Record<number, string | null>>({});

  // Her slayt iÃ§in bir ref oluÅŸtur
  const slideRefs = useRef<RefObject<HTMLDivElement>[]>([]);
  slideRefs.current = slides.map((_, i) => slideRefs.current[i] ?? createRef<HTMLDivElement>());

  useEffect(() => {
    setSlides(layouts[activeLayoutKey].slides);
    setImages({}); // Layout deÄŸiÅŸtiÄŸinde resimleri sÄ±fÄ±rla
  }, [activeLayoutKey]);

  const handleSlideUpdate = (id: number, updatedData: Partial<SlideContent>) => {
    setSlides(prevSlides =>
      prevSlides.map(slide =>
        slide.id === id ? { ...slide, ...updatedData } : slide
      )
    );
  };

  const handleImageUpload = (e: ChangeEvent<HTMLInputElement>, id: number) => {
    const file = e.target.files?.[0];
    if (file) {
      if (images[id]) {
        URL.revokeObjectURL(images[id] as string);
      }
      const url = URL.createObjectURL(file);
      setImages(prev => ({ ...prev, [id]: url }));
    }
  };

  const downloadAll = async () => {
    for (let i = 0; i < slides.length; i++) {
      const slide = slides[i];
      const node = slideRefs.current[i]?.current;
      if (node) {
        try {
          const dataUrl = await htmlToImage.toPng(node, { pixelRatio: 2 });
          download(dataUrl, `kidsai-post-${slide.id}.png`);
        } catch (error) {
          console.error(`Failed to generate image for slide ${slide.id}`, error);
        }
      }
    }
  };

  return (
    <main className="min-h-screen p-4 sm:p-8 font-body text-gray-800 bg-gray-50">
      <Header onDownloadAll={downloadAll} />
      
      <div className="max-w-4xl mx-auto">
        <div className="mb-8 p-4 bg-white rounded-2xl shadow-sm border border-gray-100">
          <h2 className="text-sm font-bold text-gray-500 uppercase tracking-wider mb-3">DÃ¼zen SeÃ§imi</h2>
          <div className="flex flex-wrap gap-2">
            {Object.keys(layouts).map(key => (
              <Button
                key={key}
                variant={activeLayoutKey === key ? 'default' : 'outline'}
                onClick={() => setActiveLayoutKey(key)}
                className={activeLayoutKey === key ? 'bg-black text-white' : ''}
              >
                {layouts[key].name}
              </Button>
            ))}
          </div>
        </div>

        <div className="space-y-8">
        {slides.map((slide, index) => (
          <TemplateCard 
            key={slide.id}
            slide={slide}
            image={images[slide.id]}
            handleImageUpload={handleImageUpload}
            handleSlideUpdate={handleSlideUpdate}
            postRef={slideRefs.current[index]}
            TemplateComponent={PostTemplate}
          />
        ))}
        </div>
      </div>
    </main>
  );
}
